.hero-border {
  width: auto;
  height: 5px;
  text-align: center;
  border-top: 5px solid;
  border-image: linear-gradient(
      to right,
      #9bcb50 5.5%,
      #fd7d24 11%,
      #4592c4 16.5%,
      rgb(235, 209, 38) 22%,
      #50c4e7 27.5%,
      #b87ec8 33%,
      #ab9842 38.5%,
      #b7c1c3 44%,
      rgb(219, 77, 207) 49.5%,
      #7b62a2 55%,
      #719f3e 60.5%,
      #a28c21 66%,
      #452fd8 71.5%,
      #a3acae 77%,
      rgb(255, 148, 191) 82.5%,
      #707070 88%,
      #d56723 93.5%,
      #9db7b7 99%
    )
    5;
}

.grass {
  background-color: #9bcb50;
  color: #232322;
}

.fire {
  background-color: #fd7d24;
}

.water {
  background-color: #4592c4;
}

.electric {
  background-color: rgb(235, 209, 38);
  color: #232322;
}

.ice {
  background-color: #50c4e7;
  color: #232322;
}

.fighting {
  background-color: #d56723;
}

.poison {
  background-color: #b87ec8;
}

.ground {
  background-color: #ab9842;
}

.flying {
  background: linear-gradient(to bottom, rgb(161, 215, 230) 50%, #b7c1c3 50%);
  color: #232322;
}

.psychic {
  background-color: rgb(219, 77, 207);
}

.ghost {
  background-color: #7b62a2;
}

.bug {
  background-color: #719f3e;
}

.rock {
  background-color: #a28c21;
}

.dragon {
  background: linear-gradient(to bottom, #452fd8 50%, #d77132 50%);
}

.normal {
  background-color: #a3acae;
}

.fairy {
  background-color: rgb(255, 148, 191);
  color: #232322;
}

.dark {
  background-color: #707070;
}

.steel {
  background-color: #9db7b7;
}

@media only screen and (max-width: 400px) {
  .fighting {
    /* padding-right: 2em;
    padding-left: 1.4em; */
    padding: 0px auto;
    transition: all 0.3s ease;
  }
}

@media only screen and (max-width: 768px) and (min-width: 854px) {
  .fighting {
    padding-right: 4.2em;
    padding-left: 0.3em;
  }
}

@media only screen and (max-width: 1024px) and (min-width: 1211px) {
  .fighting {
    padding-right: 3.85em;
    padding-left: 0.15em;
  }
  .psychic {
    padding-right: 3.66em;
    padding-left: 0.15em;
  }
  .ground,
  .electric {
    padding-right: 3.7em;
    padding-left: 0.3em;
  }
  .normal,
  .dragon {
    padding-right: 3.5em;
    padding-left: 0.1em;
  }
}

.hp {
  color: #fff;
  background-color: #29b22d;
}

.attack {
  color: #eee;
  background-color: #f4423a;
}

.defense {
  color: #eee;
  background-color: #2297f4;
}
.special-defense {
  color: #eee;
  background-color: #2072aa;
}
.special-attack {
  color: #eee;
  background-color: #3e3099;
}
.speed {
  color: #444;
  background-color: #afe6f6;
}

